#login {
	background-image: url("https://magdeleine.co/wp-content/uploads/2020/07/selfie-hikers-on-cobblestone-hill_4460x4460-1400x933.jpg");
	background-attachment: fixed;
	background-repeat: no-repeat;
	background-size: cover;
	height: 1000px;
	top: 24px;
	padding-left : 20%;
	padding-right: 20%;
	position: relative;
}

#login-form {
	display: flex;
	flex-direction: column;
}

#login-form .form {
	margin: 8px;
	min-width: 300px;
	width: 50%;
}

#login-form .button {
	left: 40%;
	margin: 8px;
	position: relative;
    width: 96px;
}

#login-form h3 {
	color: var(--warning-color1);
	margin-left: 8px;
}

#login-form p {
    font-size: 16px;
    margin-left: 8px;
}