#register {
	background-image: url("https://magdeleine.co/wp-content/uploads/2023/01/StockSnap_HGRFPBWTEJ-1400x730.jpg");
	background-attachment: fixed;
	background-repeat: no-repeat;
	background-size: cover;
	height: 1000px;
	top: 24px;
	padding-left : 20%;
	padding-right: 20%;
	position: relative;
}

#register-form {
	display: flex;
	flex-direction: column;
}

#register-form .form {
	margin: 8px;
	min-width: 300px;
	width: 50%;
}

#register-form .button {
	left: 40%;
	margin: 8px;
	position: relative;
    width: 96px;
}

#register-form h3 {
	color: var(--warning-color1);
	margin-left: 8px;
}

#register-form p {	
    font-size: 16px;
    margin-left: 8px;
}